.card {
    width: calc(20% - 20px);
    margin: 10px 10px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.24);
    border-radius: 7px;
    background-color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    position: relative;
}
.imgCont {
    padding: 10px 20px;
    height: 200px;
    text-align: center;
}
.imgCont img{
    border-radius: 400px;
}
.infoCont > h4 {
    text-align: center;
    margin: 0;
    letter-spacing: 1px;
    padding: 5px;
    margin-top: -60px;
}
.infoCont > h4 > span {
    background-color: rgba(96, 211, 96, 0.699);
    border-radius: 50px;
    padding: 3px;
    font-size: 14px;
    box-shadow: 0 0 2px rgba(96, 211, 96, 0.699) ;
    color: rgba(0, 0, 0, 0.692);
}
.infoCont > div {
    display: flex;
    border-top: 1px solid rgba(0, 0, 0, 0.336);
}
.infoCont > div > div {
    width: 50%;
    padding: 5px;
    text-align: center;
    color: rgba(0, 0, 0, 0.507);
}
.infoCont > div > div:nth-of-type(1) {
    border-right: 1px solid rgba(0, 0, 0, 0.336);
}
.infoCont > h5 {
    margin: 0;
    display: flex;
    text-align: center;
    padding: 5px;
    color: rgba(0, 0, 0, 0.616);
}
.infoCont > h5 > span {
    display: block;
    width: 50%;
}

.secDiv {
    position: absolute;
    width: 100%;
    top: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.747);
    opacity: 0;
}
.secDiv:hover {
    transition-property: opacity;
    transition-duration: 600ms;
    opacity: 1;
}
.secDiv > h5 {
    margin: 0;
    padding: 10px;
    letter-spacing: 1px;
    color: white;
    text-shadow: 0 0 2px rgba(255, 255, 255, 0.774);
}

@media (max-width:1000px) {
    .card {
        width: calc(25% - 20px);
    }
}

@media (max-width:786px) {
    .card {
        width: calc(33.33% - 20px);
    }
}

@media (max-width:570px) {
    .card {
        width: calc(50% - 20px);
    }
}

@media (max-width:380px) {
    .card {
        width: calc(100% - 20px);
    }
}